import testData from "./../data.json";

// Получаем рандомное значение элемента для заполнения формы
const getRandomIndex = (max) => {
  return Math.floor(Math.random() * max);
};

// Формируем случайный массив из data.json
export const getRandomData = () => {
  const index = getRandomIndex(testData.person.length);
  return testData.person[index];
};


