import { setSelectionRange } from "@testing-library/user-event/dist/utils";
import { useState } from "react";
import { serverPath } from "../../variables";
import {getRandomData} from "../../helpers/textData";

const Form = () => {
  // Создание состояния для тех инпутов, в которые мы запишем значения
  const [name, setName] = useState(getRandomData().name);
  const [phone, setPhone] = useState(getRandomData().phone);
  const [email, setEmail] = useState(getRandomData().email);
  const [product, setProduct] = useState(getRandomData().product);

  const insertTestData = () => {
    setName(getRandomData().name);
    setPhone(getRandomData().phone);
    setEmail(getRandomData().email);
    setProduct(getRandomData().product);
  }

  const formData = (e) => {
    e.preventDefault();

    // объект с данными, для дальнейшего создания пропсов
    const data = {
      name, 
      phone,
      email, 
      product, 
      status: "new", 
      date: new Date()
    };

    // феч запрос на сохранение заявок новых
    fetch(serverPath + "/person", {
      method: "POST", 
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(data),
      }).then((res) => {
          if(res.ok) {
            insertTestData();
          }
      });
  };

  return (
    <form id="form" method="POST" action="" onSubmit={(e) => formData(e)}> 
      <label>Ваши данные:</label>
      <div className="form-group">
        <input
          id="name"
          type="text"
          name="name"
          autoComplete="on"
          className="form-control"
          placeholder="Имя и Фамилия"
          required
          value={name}
          onChange={(e) => {setName(e.target.value)}}
        />
      </div>
      <div className="form-group">
        <input
          id="phone"
          type="text"
          name="phone"
          autoComplete="on"
          className="form-control"
          placeholder="Телефон"
          value={phone}
          onChange={(e) => {setPhone(e.target.value)}}
        />
      </div>
      <div className="form-group">
        <input
          id="email"
          type="email"
          name="email"
          autoComplete="on"
          className="form-control"
          placeholder="Email"
          required
          value={email}
          onChange={(e) => {setEmail(e.target.value)}}
        />
      </div>
      <div className="form-group">
        <label htmlFor="exampleFormControlSelect1">Продукт:</label>
        <select 
          id="product" 
          name="product" 
          className="form-control"          
          value={product}
          onChange={(e) => {setProduct(e.target.value)}}>
          <option value="course-html">Курс по верстке</option>
          <option value="course-js">Курс по JavaScript</option>
          <option value="course-vue">Курс по VUE JS</option>
          <option value="course-php">Курс по PHP</option>
          <option value="course-wordpress">Курс по WordPress</option>
        </select>
      </div>
      <div className="form-group">
        <button type="submit" className="btn btn-lg btn-primary btn-block">
          Оформить заявку
        </button>
      </div>
    </form>
  );
};

export default Form;
