import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { serverPath } from "../../variables";
import TableTasks from "./tableTasks";
import { arrayStatusBar } from "../../helpers/const";
import axios from "axios";

const Title = () => {
    const [tasks, setTasks] = useState();
    const [filter, setFilter] = useState({products: "all", status: "all"});

    useEffect(() => {
        document.body.classList = 'with-nav body--dashboard'
        axios.get(serverPath + '/person', 
            {params: {
                status: filter.status === "all" ? undefined : filter.status, 
                product: filter.products === "all" ? undefined : filter.products, 
            }}).then(data => setTasks(data.data))
            }, [filter])
            
    return ( 
        <div>
            <div className="left-panel blue-skin">
                <div className="left-panel__logo">
                    <div className="left-panel__logo-title">CRM заявки</div>
                    <div className="left-panel__logo-subtitle">учебный проект webcademy</div>
                </div>

                <div className="left-panel__user clearfix">
                    <div className="left-panel__user-photo">
                        <img src="img/avatars/avatar-128.jpg" alt="Avatar" />
                    </div>
                    <div className="left-panel__user-name">Петр <br />Васильевич</div>
                </div>

                <div className="left-panel__navigation">
                    <div className="left-panel__navigation-title">Заявки</div>
                    <ul>
                        {arrayStatusBar.map(button => (
                            <li key={button.name}><Link onClick={() => {setFilter({...filter, status: button.active})}} data-value="all" data-role="left-status" className={`${button.active === filter.status ? "active" : ""}`}>{button.name}</Link></li>
                        ))}
                    </ul>
                </div>
            </div>

            <div className="main-wrapper">
                <div className="container-fluid">
                    <div className="admin-heading-1">Все заявки</div>

                    <form action="">
                        <div className="row mb-3 justify-content-start">
                            <div className="col">
                                <div id="topStatusBar" className="btn-group" role="group" aria-label="...">
                                    {arrayStatusBar.map(button => (
                                        <Link onClick={() => {setFilter({...filter, status: button.active})}}  
                                        key={button.name} className={`btn btn-light ${button.active === filter.status ? "active" : ""}`}>{button.name}</Link>
                                    ))}
                                </div>
                            </div>
                            <div className="col">
                                <select className="custom-select" id="productSelect" onClick={(e) => {setFilter({...filter, products: e.target.value})}}>
                                    <option value="all">Все продукты</option>
                                    <option value="course-html">Курс по верстке</option>
                                    <option value="course-js">Курс по JavaScript</option>
                                    <option value="course-vue">Курс по VUE JS</option>
                                    <option value="course-php">Курс по PHP</option>
                                    <option value="course-wordpress">Курс по WordPress</option>
                                </select>
                            </div>
                        </div>
                    </form>
                    <table className="table fs-14">
                        <thead>
                            <tr>
                                <th>ID</th>
                                <th>дата</th>
                                <th>продукт</th>
                                <th>имя</th>
                                <th>email</th>
                                <th>телефон</th>
                                <th>статус</th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody id="tbody">
                            {tasks && <TableTasks tasks={tasks}/>}
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    );
}

export default Title;