import { useState } from "react";
import { serverPath } from "../../variables";
import { useNavigate } from "react-router-dom";

const EditForm = ({data}) => {
    // Навигация для возврата обратно на главную страницу с заявками. Используем при редактировании заявки получая феч запрос
    const navigate = useNavigate();

    // Задаем состояния и записываем в них данные через функции 
    const [names, setNames] = useState(data.name);
    const [phone, setPhone] = useState(data.phone);
    const [email, setEmail] = useState(data.email);
    const [product, setProduct] = useState(data.product);
    const [status, setStatus] = useState(data.status);

    const onSubmit = (e) => {
        e.preventDefault();
        const task = {
            name: names,
            phone, 
            email, 
            product, 
            status, 
            date: data.date
        };

        // Феч запрос для страницы с заявками 
        fetch(serverPath + `/person/${data.id}`, {
            method: "PUT", 
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(task),
            }).then((res) => {
                if(res.ok) {
                navigate("/title");
                }
        });
    }

    // феч для удаление заявок с сервера 
    const deleteTasks = () => {
        fetch(serverPath + `/person/${data.id}`, {
            method: "DELETE", 
            headers: { 'Content-Type': 'application/json' }
            }).then((res) => {
                if(res.ok) {
                navigate("/title");
                }
        });
    }

    return ( 
            <form id="form" onSubmit={(e) => {onSubmit(e)}}>
            <div className="card mb-4">
                <div className="card-header">Данные о заявке</div>
                <div className="card-body">
                    <div className="row mb-3">
                        <div className="col-md-2">
                            <strong>ID:</strong>
                        </div>
                        <div className="col">Заявка №<span id="number">{data.id}</span></div>
                        <input name="id" type="hidden" id="id"/>
                    </div>

                    <div className="row mb-3">
                        <div className="col-md-2">
                            <strong>Дата создания:</strong>
                        </div>
                        <div className="col" id="date">
                            {new Date(data.date).toLocaleDateString()} {new Date(data.date).toLocaleTimeString()}
                        </div>
                    </div>

                    <div className="row mb-3">
                        <div className="col-md-2">
                            <strong>Продукт:</strong>
                        </div>
                        <div className="col">
                            <select id="product" name="product" className="custom-select"                                 
                                value={product}
                                onChange={(e) => setProduct(e.target.value)}>
                                <option value="course-html">Курс по верстке</option>
                                <option value="course-js">
                                    Курс по JavaScript
                                </option>
                                <option value="course-vue">Курс по VUE JS</option>
                                <option value="course-php">Курс по PHP</option>
                                <option value="course-wordpress">
                                    Курс по WordPress
                                </option>
                            </select>
                        </div>
                    </div>

                    <div className="row mb-3">
                        <div className="col-md-2">
                            <strong>Имя:</strong>
                        </div>
                        <div className="col">
                            <input
                                type="text"
                                className="form-control"
                                id="name"
                                name="name"
                                value={names}
                                onChange={(e) => setNames(e.target.value)}
                            />
                        </div>
                    </div>

                    <div className="row mb-3">
                        <div className="col-md-2">
                            <strong>Email:</strong>
                        </div>
                        <div className="col">
                            <input
                                type="text"
                                className="form-control"
                                id="email"
                                name="email"
                                value={email}
                                onChange={(e) => {setEmail(e.target.value)}}
                                />
                        </div>
                    </div>

                    <div className="row mb-3">
                        <div className="col-md-2">
                            <strong>Телефон:</strong>
                        </div>
                        <div className="col">
                            <input
                                type="text"
                                className="form-control"
                                id="phone"
                                name="phone"
                                value={phone}
                                onChange={(e) => {setPhone(e.target.value)}}
                                />
                        </div>
                    </div>

                    <div className="row mb-3">
                        <div className="col-md-2">
                            <strong>Статус заявки:</strong>
                        </div>
                        <div className="col">
                            <select className="custom-select" 
                                id="status" 
                                name="status"                                 
                                value={status}
                                onChange={(e) => {setStatus(e.target.value)}}>
                                <option select="">Выберите...</option>
                                <option value="new">Новая</option>
                                <option value="inwork">В работе</option>
                                <option value="complete">Завершена</option>
                            </select>
                        </div>
                    </div>
                </div>
            </div>
            <div className="row justify-content-between">
                <div className="col text-right">
                    <button type="submit" className="btn btn-primary">Сохранить изменения</button>
                </div>
                <div className="col text-right">
                    <button type="button" className="btn btn-primary" onClick={() => {deleteTasks()}}>Удалить заявку</button>
                </div>
            </div>
        </form>
    );
}

export {EditForm};