import { Link } from "react-router-dom";

const badges = {
    new: 'badge-danger',
    inwork: 'badge-warning',
    complete: 'badge-success'
}

const products = {
    'course-html': 'Курс по верстке',
    'course-js': 'Курс по JavaScript',
    'course-vue': 'Курс по Vue JS',
    'course-php': 'Курс по PHP',
    'course-wordpress': 'Курс по WordPress',
}

const statuses = {
    'new': 'Новая',
    'inwork': 'В работе',
    'complete': 'Завершена',
}

const TableTasks = ({tasks}) => {
    console.log(tasks);
    return ( 
        <>
            {tasks.map((task, index) => (
                <tr key={task.id}>
                    <th scope="row">{index}</th>
                    <td>{new Date(task.date).toLocaleDateString()}</td>
                    <td>{products[task.product]}</td>
                    <td>{task.name}</td>
                    <td>{task.email}</td>
                    <td>{task.phone}</td>
                    <td>
                        <div className={`badge badge-pill ${badges[task.status]}`}>{statuses[task.status]}</div>
                    </td>
                    <td>
                        <Link to={`/edit/${task.id}`}>Редактировать</Link>
                    </td>
                </tr>
            ))}
        </>
    );
}
// Работа с формой, заполнение заявки и редактирование в зависимости от id

export default TableTasks;