import { Link, useParams } from "react-router-dom";
import { useEffect, useState } from "react";
import { EditForm } from "./editForm";
import { serverPath } from "../../variables";

const Edit = () => {
    const [data, setData] = useState();

    const {id} = useParams(); 

    useEffect(() => {
        document.body.classList = 'with-nav'
        fetch(serverPath + `/person/${id}`)			
        .then((res) => res.json())
        .then((data) => {
            setData(data);
        });
    }, [id])
    
    return (
        <div>
            <div className="form-wrapper">
                <div className="container-fluid">
                    <div className="row justify-content-between align-items-center">
                        <div className="col">
                            <div className="admin-heading-1">Работа с заявкой</div>
                        </div>
                        <div className="col text-right">
                            <Link to='/title' className="btn btn-link">Вернуться назад</Link>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col">
                            {data && <EditForm data={data}/>}
                        </div>
                    </div>
                </div>
            </div>
        </div> 
    );
}

export default Edit;